.gallery {
    display: grid;
    grid-template-columns: 200px 1fr;
    h1 {
        grid-column: 1/-1;
        justify-self: center; }
    &__big-image {
        grid-column: 2/3;
        img {
            width: 100%;
            height: 100%; } }


    &__list {
        margin: 0;
        padding: 0;
        list-style-type: none;
        display: grid;
        grid-template-rows: repeat(5,1fr );
 }        // margin-top: 20px
